import Vue from 'vue'
import html2canvas from 'html2canvas'



const PDF = {}
PDF.install = function (Vue, options) {
  /**
   * targetDom: dom
   * name:pdf
   * callback: 
   */
  Vue.prototype.$createPdf = function (targetDom, name, callback) {
    // console.log(window.devicePixelRatio * 4)
    // a4[592.28,841.89]
    uni.showLoading({
      title: ''
    })
    const A4_WIDTH = 592.28
    const A4_HEIGHT = 841.89 
    // pdf A4
    let pageHeight = targetDom.scrollWidth / A4_WIDTH * A4_HEIGHT
    //domdomdom
    let cloneDom = targetDom.cloneNode(true)
    cloneDom.style.width = targetDom.scrollWidth + 'px'
    cloneDom.style.background = '#FFFFFF'
    // cloneDom.style.display = 'none'
    document.body.appendChild(cloneDom)
    //divclassclassPDF
    let pages = cloneDom.childNodes
    //
    for (let i = 0; i < pages.length; i++) {
      //PDFPDF2PDF/PDFPDF*PDFPDF
      if (pages[i].offsetHeight > pageHeight) {
        let num = Math.ceil(pages[i].offsetHeight / pageHeight)
        pages[i].style.height = pageHeight * num + 'px'
      } else {
        // pages[i].style.height = pageHeight + 'px'
      }
	  
      //div
      // pages[i].style.marginBottom = 0 + 'px'
    }
	 
    html2canvas(cloneDom, {
      scale: 4, // 
      useCORS: true, //  
      imageTimeout: 0, // 0
      height: cloneDom.scrollHeight, // canvasdomdomdom
      width: cloneDom.scrollWidth, // canvas
      windowWidth: document.body.scrollWidth,
      windowHeight: document.body.scrollHeight,
      dpi: 172, // DPI
      logging: false,
      allowTaint: false,
    }).then(canvas => {
      document.body.removeChild(cloneDom) // 
      const _this = this;
      //pdfhtml
      var leftHeight = canvas.height;
      var a4Width = 555.28 // A4 592  20 , 40
      var a4Height = 801.89 // A4   841  20 , 40
      //pdfhtmlcanvas;
      var a4HeightRef = Math.floor(canvas.width / a4Width * a4Height);
 
      //pdf
      var position = 0;
 
      var pageData = canvas.toDataURL('image/jpeg', 1.0);
 
      var pdf = new JsPDF('x', 'pt', 'a4');
      var index = 1,
        canvas1 = document.createElement('canvas'),
        height;
      pdf.setDisplayMode('fullwidth', 'continuous', 'FullScreen');
 
      function createImpl(canvas) {
        if (leftHeight > 0) {
          index++;
          var checkCount = 0;
          if (leftHeight > a4HeightRef) {
            var i = position + a4HeightRef;
            for (i = position + a4HeightRef; i >= position; i--) {
              var isWrite = true
              for (var j = 0; j < canvas.width; j++) {
                var c = canvas.getContext('2d').getImageData(j, i, 1, 1).data
 
                if (c[0] != 0xff || c[1] != 0xff || c[2] != 0xff) {
                  isWrite = false
                  break
                }
              }
              if (isWrite) {
                checkCount++
                if (checkCount >= 10) {
                  break
                }
              } else {
                checkCount = 0
              }
            }
            height = Math.round(i - position) || Math.min(leftHeight, a4HeightRef);
            if (height <= 0) {
              height = a4HeightRef;
            }
          } else {
            height = leftHeight;
          }
 
          canvas1.width = canvas.width;
          canvas1.height = height;
 
          // console.log(index, 'height:', height, 'pos', position);
 
          var ctx = canvas1.getContext('2d');
          ctx.drawImage(canvas, 0, position, canvas.width, height, 0, 0, canvas.width, height); // 0
 
          var pageHeight = Math.round(a4Width / canvas.width * height);
          // pdf.setPageSize(null, pageHeight)
          if (position != 0) {
            pdf.addPage();
          }
          //  20px 
          pdf.addImage(canvas1.toDataURL('image/jpeg', 1.0), 'JPEG', 20, 20, a4Width, a4Width / canvas1.width * height);
          leftHeight -= height;
          position += height;
          // $('.pdfProgress').text(index + 1);
          // $('.pdfTotal').text(index + Math.ceil(leftHeight / a4HeightRef));
          if (leftHeight > 0) {
            //
            const base64 = 'data:image/png;base64,'
            for (let i = 0; i < 6; i++) {
              for (let j = 0; j < 5; j++) {
                const left = (j * 120) + 20;
                // pdf.addImage(base64,'JPEG', left, i*150, 20, 30); // 
              }
            }
            setTimeout(createImpl, 500, canvas);
          } else {
            pdf.save(name + '.pdf');
            uni.hideLoading()
          }
        }
      }
 
      //pdf
      if (leftHeight < a4HeightRef) {
        pdf.addImage(pageData, 'JPEG', 20, 20, a4Width, a4Width / canvas.width * leftHeight);
        pdf.save('pdf' + '.pdf')
        uni.hideLoading()
      } else {
        try {
          pdf.deletePage(0);
          setTimeout(createImpl, 500, canvas);
 
        } catch (err) {
          console.log(err);
        }
      }
    })
  }
}
Vue.use(PDF)
export default PDF